  /**
   * ì¸ì¦ ë°ì´í„° ì„¤ì •
   */
  private setAuthData(response: LoginResponse): void {
    localStorage.setItem(this.TOKEN_KEY, response.token);
    localStorage.setItem(this.REFRESH_TOKEN_KEY, response.refreshToken);
    localStorage.setItem(this.USER_KEY, JSON.stringify(response.user));

    this.updateAuthState({
      user: response.user,
      token: response.token,
      refreshToken: response.refreshToken,
      isAuthenticated: true,
      isLoading: false,
      error: null
    });
  }

  /**
   * ì¸ì¦ ë°ì´í„° ì‚­ì œ
   */
  private clearAuthData(): void {
    localStorage.removeItem(this.TOKEN_KEY);
    localStorage.removeItem(this.REFRESH_TOKEN_KEY);
    localStorage.removeItem(this.USER_KEY);

    if (this.refreshTimer) {
      clearTimeout(this.refreshTimer);
      this.refreshTimer = null;
    }

    this.updateAuthState({
      user: null,
      token: null,
      refreshToken: null,
      isAuthenticated: false,
      isLoading: false,
      error: null
    });

    this.router.navigate(['/project/auth/login']);
  }

  /**
   * ì¸ì¦ ìƒíƒœ ì—…ë°ì´íŠ¸
   */
  private updateAuthState(state: Partial<AuthState>): void {
    this.authStateSubject.next({
      ...this.authStateSubject.value,
      ...state
    });
  }

  /**
   * í† í° ê°±ì‹  íƒ€ì´ë¨¸ ì‹œì‘
   */
  private startTokenRefreshTimer(): void {
    if (this.refreshTimer) {
      clearTimeout(this.refreshTimer);
    }

    // í† í° ë§Œë£Œ 15ë¶„ ì „ì— ê°±ì‹ 
    const refreshTime = 45 * 60 * 1000; // 45ë¶„

    this.refreshTimer = setTimeout(() => {
      this.refreshToken().subscribe({
        error: () => this.clearAuthData()
      });
    }, refreshTime);
  }

  /**
   * ì¸ì¦ ì—ëŸ¬ ì²˜ë¦¬
   */
  private handleAuthError(error: HttpErrorResponse): Observable<never> {
    let authError: AuthError;

    if (error.error instanceof ErrorEvent) {
      // í´ë¼ì´ì–¸íŠ¸ ì‚¬ì´ë“œ ì—ëŸ¬
      authError = {
        code: 'CLIENT_ERROR',
        message: 'ë„¤íŠ¸ì›Œí¬ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.',
        details: error.error.message
      };
    } else {
      // ì„œë²„ ì‚¬ì´ë“œ ì—ëŸ¬
      authError = {
        code: error.error?.code || 'SERVER_ERROR',
        message: error.error?.message || 'ì„œë²„ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.',
        details: error.error
      };
    }

    this.updateAuthState({
      ...this.authStateSubject.value,
      isLoading: false,
      error: authError.message
    });

    return throwError(authError);
  }
