import { Injectable } from '@angular/core';
import { CanActivate, CanActivateChild, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { ProjectAuthService } from '../services/auth.service';

@Injectable({
  providedIn: 'root'
})
export class ProjectAuthGuard implements CanActivate, CanActivateChild {

  constructor(
    private authService: ProjectAuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> {
    return this.checkAuth(state.url);
  }

  canActivateChild(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> {
    return this.checkAuth(state.url);
  }

  private checkAuth(url: string): Observable<boolean> {
    return this.authService.authState$.pipe(
      map(authState => {
        if (authState.isAuthenticated) {
          return true;
        } else {
          this.router.navigate(['/project/auth/login'], { 
            queryParams: { returnUrl: url } 
          });
          return false;
        }
      }),
      catchError(() => {
        this.router.navigate(['/project/auth/login'], { 
          queryParams: { returnUrl: url } 
        });
        return of(false);
      })
    );
  }
}

@Injectable({
  providedIn: 'root'
})
export class ProjectGuestGuard implements CanActivate {

  constructor(
    private authService: ProjectAuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> {
    return this.authService.authState$.pipe(
      map(authState => {
        if (!authState.isAuthenticated) {
          return true;
        } else {
          this.router.navigate(['/project']);
          return false;
        }
      })
    );
  }
}

@Injectable({
  providedIn: 'root'
})
export class ProjectRoleGuard implements CanActivate {

  constructor(
    private authService: ProjectAuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> {
    const requiredRoles = route.data['roles'] as string[];
    
    if (!requiredRoles || requiredRoles.length === 0) {
      return of(true);
    }

    return this.authService.authState$.pipe(
      map(authState => {
        if (!authState.isAuthenticated || !authState.user) {
          this.router.navigate(['/project/auth/login']);
          return false;
        }

        const hasRole = requiredRoles.includes(authState.user.role);
        if (!hasRole) {
          this.router.navigate(['/project/access-denied']);
          return false;
        }

        return true;
      })
    );
  }
}