export class ProjectForgotPasswordComponent implements OnInit, OnDestroy {
  forgotPasswordForm: FormGroup;
  isLoading = false;
  emailSent = false;
  sentEmail = '';
  resendCooldown = 0;
  private destroy$ = new Subject<void>();
  private cooldownTimer: any;

  constructor(
    private fb: FormBuilder,
    private authService: ProjectAuthService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {
    this.forgotPasswordForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]]
    });
  }

  ngOnInit(): void {
    // ë¡œë”© ìƒíƒœ êµ¬ë…
    this.authService.authState$
      .pipe(takeUntil(this.destroy$))
      .subscribe((authState: AuthState) => {
        this.isLoading = authState.isLoading;

        if (authState.error) {
          this.showError(authState.error);
        }
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    if (this.cooldownTimer) {
      clearInterval(this.cooldownTimer);
    }
  }

  onSubmit(): void {
    if (this.forgotPasswordForm.valid) {
      const email = this.forgotPasswordForm.value.email;
      const request: PasswordResetRequest = { email };

      this.authService.requestPasswordReset(request).subscribe({
        next: () => {
          this.sentEmail = email;
          this.emailSent = true;
          this.showSuccess('ë¹„ë°€ë²ˆí˜¸ ì¬ì„¤ì • ì´ë©”ì¼ì„ ì „ì†¡í–ˆìŠµë‹ˆë‹¤.');
          this.startResendCooldown();
        },
        error: (error: any) => {
          this.showError(error.message || 'ì´ë©”ì¼ ì „ì†¡ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.');
        }
      });
    }
  }

  resendEmail(): void {
    if (this.resendCooldown === 0) {
      this.onSubmit();
    }
  }

  private startResendCooldown(): void {
    this.resendCooldown = 60; // 60ì´ˆ ì¿¨ë‹¤ìš´
    this.cooldownTimer = setInterval(() => {
      this.resendCooldown--;
      if (this.resendCooldown <= 0) {
        clearInterval(this.cooldownTimer);
      }
    }, 1000);
  }

  private showSuccess(message: string): void {
    this.snackBar.open(message, 'ë‹«ê¸°', {
      duration: 3000,
      horizontalPosition: 'center',
      verticalPosition: 'top'
    });
  }

  private showError(message: string): void {
    this.snackBar.open(message, 'ë‹«ê¸°', {
      duration: 5000,
      horizontalPosition: 'center',
      verticalPosition: 'top'
    });
  }
}