import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Router, ActivatedRoute, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { Subject, takeUntil } from 'rxjs';
import { ProjectAuthService } from '../../../services/auth.service';
import { SharedModule } from '../../../../shared/shared.module';
import { LoginConfig, UnifiedLoginComponent } from '../../../../shared/components/unified-login/unified-login.component';
import { environment } from '../../../../../../environments/environment';

@Component({
  selector: 'app-project-login',
  standalone: true,
  imports: [
    CommonModule,
    RouterModule,
    SharedModule
  ],
  template: '<app-unified-login #loginComponent [config]="loginConfig" (loginSubmit)="onLogin($event)" (registerClick)="onRegisterClick()"></app-unified-login>'
})
export class ProjectLoginComponent implements OnInit, OnDestroy {
  @ViewChild('loginComponent') loginComponent?: UnifiedLoginComponent;

  loginConfig: LoginConfig = {
    title: 'ğŸ“Š í”„ë¡œì íŠ¸ ê´€ë¦¬',
    subtitle: 'íš¨ìœ¨ì ì¸ í”„ë¡œì íŠ¸ í˜‘ì—…',
    moduleName: 'project',
    showSocialLogin: false,
    showRegisterLink: true,
    themeColors: {
      primary: '#8b5cf6',    // ë³´ë¼ (í”„ë¡œì íŠ¸/ê´€ë¦¬)
      secondary: '#7c3aed',  // ì§„í•œ ë³´ë¼
      accent: '#a78bfa'      // ë°ì€ ë³´ë¼
    }
  };

  private destroy$ = new Subject<void>();
  private returnUrl: string = '/project';

  constructor(
    private authService: ProjectAuthService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    // returnUrl íŒŒë¼ë¯¸í„° ê°€ì ¸ì˜¤ê¸°
    this.route.queryParams
      .pipe(takeUntil(this.destroy$))
      .subscribe(params => {
        this.returnUrl = params['returnUrl'] || '/project';
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  async onLogin(credentials: { username: string; password: string }): Promise<void> {
    if (this.loginComponent) {
      this.loginComponent.setLoading(true);
    }

    try {
      await this.authService.login({
        username: credentials.username,
        password: credentials.password
      }).toPromise();

      // ë¡œê·¸ì¸ ì„±ê³µ
      this.router.navigateByUrl(this.returnUrl);
    } catch (error: any) {
      // ë¡œê·¸ì¸ ì‹¤íŒ¨
      if (this.loginComponent) {
        const errorMessage = error?.error?.message || 'ë¡œê·¸ì¸ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.';
        this.loginComponent.setError(errorMessage);
        this.loginComponent.setLoading(false);
      }
    }
  }

  onRegisterClick(): void {
    this.router.navigate(['/project/register']);
  }
}
